* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

body {
	font-family: "Inter", sans-serif;
	background-color: #040202;
	color: white;
	overflow-x: hidden;
}

a {
	text-decoration: none;
	color: inherit;
}

button {
	cursor: pointer;
	border: none;
	outline: none;
	font-family: inherit;
}

img {
	max-width: 100%;
	display: block;
}
